export class API {
  static getTreeObj(objId: string, context) {
    let TREE_URL = `${API_URL}benefit-plans/tree/${objId}`;

    if (context !== '') {
      TREE_URL = `${API_URL}${context}/tree/${objId}`;
    }
    
    return fetch(TREE_URL, {
      method: 'get',  
      headers: { 'Content-Type': 'application/json' },
    })
    .then(response => response.json())
    .catch(error => {
      console.log('client error: ', error);
    });
  }
}